.page {
  padding: (51px - $card_border-size) 0;
  @extend %center_block;

  &__container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;
    width: (4 * $card_width) 
      + (3 * distance-without-border($card_margin-right));
  }
}
